@font-face {
  font-family: "Oswald";
  src: url("../../fonts/oswaldregular.woff2") format("woff2"), url("../../fonts/oswaldregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Oswald";
  src: url("../../fonts/oswaldmedium.woff2") format("woff2"), url("../../fonts/oswaldmedium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
}
img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

.page__body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Oswald", Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  font-style: normal;
  min-height: 100%;
  /*Прилипший футер*/
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}

.page-header {
  display: flex;
  justify-content: space-between;
}
.page-header__logo {
  height: 65px;
  outline: 2px solid red;
  outline-offset: -2px;
}
.page-header a {
  text-decoration: none;
}

.header-logo {
  display: flex;
}
.header-logo__img {
  width: 33px;
  height: 37px;
}
.header-logo__text {
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .header-logo {
    height: 76px;
  }
}
@media (min-width: 1024px) {
  .header-logo {
    height: 112px;
  }
}

.main-nav {
  outline: 2px solid #137813;
  outline-offset: -2px;
}

@media (min-width: 768px) {
  .main-nav__wrapper {
    width: 440px;
  }
}
@media (min-width: 1024px) {
  .main-nav__wrapper {
    width: 478px;
  }
}

.site-list {
  list-style: none;
}

.slider__wrapper {
  color: #ffffff;
}

.slider__header {
  background-color: green;
  height: 455px;
  margin-bottom: 138px;
}
.slider__header__title {
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
}

.slider__img {
  width: 280px;
  height: 270px;
}
@media (min-width: 768px) {
  .slider__img {
    width: 709px;
    height: 609px;
  }
}
@media (min-width: 1024px) {
  .slider__img {
    width: 552px;
    height: 499px;
  }
}